	module.exports = {"data":{"postsJson":{"title":"1차 선교여행","questionsTitle":"경건한 본보기를 따르기","display":"block","bookName":"사도행전","ref":"13:1-14:28","html":"<p>   <strong>13</strong><u>1</u>안디옥 교회에 선지자들과 교사들이 있으니 곧 바나바와 니게르라 하는 시므온과 구레네 사람 루기오와 분봉 왕 헤롯의 젖동생 마나엔과 및 사울이라 <u>2</u>주를 섬겨 금식할 때에 성령이 이르시되</p><h1>하나님</h1><h2><em>내가 불러 시키는 일을 위하여 바나바와 사울을 따로 세우라</em></h2><p>하시니 <u>3</u>이에 금식하며 기도하고 두 사람에게 안수하여 보내니라<br />   <u>4</u>두 사람이 성령의 보내심을 받아 실루기아에 내려가 거기서 배 타고 구브로에 가서 <u>5</u>살라미에 이르러 하나님의 말씀을 유대인의 여러 회당에서 전할새 요한을 수행원으로 두었더라<br />   <u>6</u>온 섬 가운데로 지나서 바보에 이르러 바예수라 하는 유대인 거짓 선지자인 마술사를 만나니 <u>7</u>그가 총독 서기오 바울과 함께 있으니 서기오 바울은 지혜 있는 사람이라 바나바와 사울을 불러 하나님의 말씀을 듣고자 하더라 <u>8</u>이 마술사 엘루마는 (이 이름을 번역하면 마술사라) 그들을 대적하여 총독으로 믿지 못하게 힘쓰니 <u>9</u>바울이라고 하는 사울이 성령이 충만하여 그를 주목하고 <u>10</u>이르되</p><h1>바울</h1><h3>모든 거짓과 악행이 가득한 자요 마귀의 자식이요 모든 의의 원수여 주의 바른 길을 굽게 하기를 그치지 아니하겠느냐 <u>11</u>보라 이제 주의 손이 네 위에 있으니 네가 맹인이 되어 얼마 동안 해를 보지 못하리라</h3><p>하니 즉시 안개와 어둠이 그를 덮어 인도할 사람을 두루 구하는지라<br />   <u>12</u>이에 총독이 그렇게 된 것을 보고 믿으며 주의 가르치심을 놀랍게 여기니라</h1><p><u>13</u>바울과 및 동행하는 사람들이 바보에서 배 타고 밤빌리아에 있는 버가에 이르니 요한은 그들에게서 떠나 예루살렘으로 돌아가고 <u>14</u>그들은 버가에서 더 나아가 비시디아 안디옥에 이르러 안식일에 회당에 들어가 앉으니라 <u>15</u>율법과 선지자의 글을 읽은 후에 회당장들이 사람을 보내어 물어 이르되</p><h1>회당장</h1><h4>형제들아 만일 백성을 권할 말이 있거든 말하라</h4><p>하니 <u>16</u>바울이 일어나 손짓하며 말하되</p><h1>바울</h1><h3>이스라엘 사람들과 및 하나님을 경외하는 사람들아 들으라<br />   <u>17</u>이 이스라엘 백성의 하나님이 우리 조상들을 택하시고 애굽 땅에서 나그네 된 그 백성을 높여 큰 권능으로 인도하여 내사 <u>18</u>광야에서 약 사십 년간 그들의 소행을 참으시고 <u>19</u>가나안 땅 일곱 족속을 멸하사 그 땅을 기업으로 주시기까지 약 사백오십 년간이라<br />   <u>20</u>그 후에 선지자 사무엘 때까지 사사를 주셨더니 <u>21</u>그 후에 그들이 왕을 구하거늘 하나님이 베냐민 지파 사람 기스의 아들 사울을 사십 년간 주셨다가 <u>22</u>폐하시고 다윗을 왕으로 세우시고 증언하여 이르시되 내가 이새의 아들 다윗을 만나니 내 마음에 맞는 사람이라 내 뜻을 다 이루리라 하시더니 <u>23</u>하나님이 약속하신 대로 이 사람의 후손에서 이스라엘을 위하여 구주를 세우셨으니 곧 예수라 <u>24</u>그가 오시기에 앞서 요한이 먼저 회개의 세례를 이스라엘 모든 백성에게 전파하니라 <u>25</u>요한이 그 달려갈 길을 마칠 때에 말하되 너희가 나를 누구로 생각하느냐 나는 그리스도가 아니라 내 뒤에 오시는 이가 있으니 나는 그 발의 신발끈을 풀기도 감당하지 못하리라 하였으니 <u>26</u>형제들아 아브라함의 후손과 너희 중 하나님을 경외하는 사람들아 이 구원의 말씀을 우리에게 보내셨거늘 <u>27</u>예루살렘에 사는 자들과 그들 관리들이 예수와 및 안식일마다 외우는 바 선지자들의 말을 알지 못하므로 예수를 정죄하여 선지자들의 말을 응하게 하였도다 <u>28</u>죽일 죄를 하나도 찾지 못하였으나 빌라도에게 죽여 달라 하였으니 <u>29</u>성경에 그를 가리켜 기록한 말씀을 다 응하게 한 것이라 후에 나무에서 내려다가 무덤에 두었으나 <u>30</u>하나님이 죽은 자 가운데서 그를 살리신지라<u>31</u>갈릴리로부터 예루살렘에 함께 올라간 사람들에게 여러 날 보이셨으니 그들이 이제 백성 앞에서 그의 증인이라<br />   <u>32</u>우리도 조상들에게 주신 약속을 너희에게 전파하노니 <u>33</u>곧 하나님이 예수를 일으키사 우리 자녀들에게 이 약속을 이루게 하셨다 함이라 시편 둘째 편에 기록한 바와 같이<br />   너는 내 아들이라<br />   오늘 너를 낳았다<br />   하셨고 <u>34</u>또 하나님께서 죽은 자 가운데서 그를 일으키사 다시 썩음을 당하지 않게 하실 것을 가르쳐 이르시되 내가 다윗의 거룩하고 미쁜 은사를 너희에게 주리라 하셨으며 <u>35</u>또 다른 시편에 일렀으되 주의 거룩한 자로 썩음을 당하지 않게 하시리라 하셨느니라<br />   <u>36</u>다윗은 당시에 하나님의 뜻을 따라 섬기다가 잠들어 그 조상들과 함께 묻혀 썩음을 당하였으되 <u>37</u>하나님께서 살리신 이는 썩음을 당하지 아니하였나니 <u>38</u>그러므로 형제들아 너희가 알 것은 이 사람을 힘입어 죄 사함을 너희에게 전하는 이것이며 <u>39</u>또 모세의 율법으로 너희가 의롭다 하심을 얻지 못하던 모든 일에도 이 사람을 힘입어 믿는 자마다 의롭다 하심을 얻는 이것이라 <u>40</u>그런즉 너희는 선지자들을 통하여 말씀하신 것이 너희에게 미칠까 삼가라 <u>41</u>일렀으되<br />   보라 멸시하는 사람들아<br />   너희는 놀라고 멸망하라<br />   내가 너희 때를 당하여 한 일을 행할 것이니<br />   사람이 너희에게 일러줄지라도<br />   도무지 믿지 못할 일이라 하였느니라</h3><p>하니라<br />   <u>42</u>그들이 나갈새 사람들이 청하되 다음 안식일에도 이 말씀을 하라 하더라 <u>43</u>회당의 모임이 끝난 후에 유대인과 유대교에 입교한 경건한 사람들이 많이 바울과 바나바를 따르니 두 사도가 더불어 말하고 항상 하나님의 은혜 가운데 있으라 권하니라<br />   <u>44</u>그 다음 안식일에는 온 시민이 거의 다 하나님의 말씀을 듣고자 하여 모이니 <u>45</u>유대인들이 그 무리를 보고 시기가 가득하여 바울이 말한 것을 반박하고 비방하거늘 <u>46</u>바울과 바나바가 담대히 말하여 이르되</p><h1>바울과 바나바 </h1><h3>하나님의 말씀을 마땅히 먼저 너희에게 전할 것이로되 너희가 그것을 버리고 영생을 얻기에 합당하지 않은 자로 자처하기로 우리가 이방인에게로 향하노라 <u>47</u>주께서 이같이 우리에게 명하시되<br />   내가 너를 이방의 빛으로 삼아<br />   너로 땅 끝까지 구원하게 하리라<br />   하셨느니라</h3><p>하니 <u>48</u>이방인들이 듣고 기뻐하여 하나님의 말씀을 찬송하며 영생을 주시기로 작정된 자는 다 믿더라 <u>49</u>주의 말씀이 그 지방에 두루 퍼지니라<br />   <u>50</u>이에 유대인들이 경건한 귀부인들과 그 시내 유력자들을 선동하여 바울과 바나바를 박해하게 하여 그 지역에서 쫓아내니 <u>51</u>두 사람이 그들을 향하여 발의 티끌을 떨어 버리고 이고니온으로 가거늘 <u>52</u>제자들은 기쁨과 성령이 충만하니라<br />   <strong>14</strong><u>1</u>이에 이고니온에서 두 사도가 함께 유대인의 회당에 들어가 말하니 유대와 헬라의 허다한 무리가 믿더라 <u>2</u>그러나 순종하지 아니하는 유대인들이 이방인들의 마음을 선동하여 형제들에게 악감을 품게 하거늘 <u>3</u>두 사도가 오래 있어 주를 힘입어 담대히 말하니 주께서 그들의 손으로 표적과 기사를 행하게 하여 주사 자기 은혜의 말씀을 증언하시니 <u>4</u>그 시내의 무리가 나뉘어 유대인을 따르는 자도 있고 두 사도를 따르는 자도 있는지라<br />   <u>5</u>이방인과 유대인과 그 관리들이 두 사도를 모욕하며 돌로 치려고 달려드니 <u>6</u>그들이 알고 도망하여 루가오니아의 두 성 루스드라와 더베와 그 근방으로 가서 <u>7</u>거기서 복음을 전하니라<br />   <u>8</u>루스드라에 발을 쓰지 못하는 한 사람이 앉아 있는데 나면서 걷지 못하게 되어 걸어 본 적이 없는 자라 <u>9</u>바울이 말하는 것을 듣거늘 바울이 주목하여 구원 받을 만한 믿음이 그에게 있는 것을 보고 <u>10</u>큰 소리로 이르되</p><h1>바울</h1><h3>네 발로 바로 일어서라</h3><p>하니 그 사람이 일어나 걷는지라<br />   <u>11</u>무리가 바울이 한 일을 보고 루가오니아 방언으로 소리 질러 이르되</p><h1>루스드라 사람들</h1><h4>신들이 사람의 형상으로 우리 가운데 내려오셨다</h4><p>하여 <u>12</u>바나바는 제우스라 하고 바울은 그 중에 말하는 자이므로 헤르메스라 하더라 <u>13</u>시외 제우스 신당의 제사장이 소와 화환들을 가지고 대문 앞에 와서 무리와 함께 제사하고자 하니 <u>14</u>두 사도 바나바와 바울이 듣고 옷을 찢고 무리 가운데 뛰어 들어가서 소리 질러 <u>15</u>이르되</p><h1>바울과 바나바</h1><h3>여러분이여 어찌하여 이러한 일을 하느냐 우리도 여러분과 같은 성정을 가진 사람이라 여러분에게 복음을 전하는 것은 이런 헛된 일을 버리고 천지와 바다와 그 가운데 만물을 지으시고 살아 계신 하나님께로 돌아오게 함이라 <u>16</u>하나님이 지나간 세대에는 모든 민족으로 자기들의 길들을 가게 방임하셨으나 <u>17</u>그러나 자기를 증언하지 아니하신 것이 아니니 곧 여러분에게 하늘로부터 비를 내리시며 결실기를 주시는 선한 일을 하사 음식과 기쁨으로 여러분의 마음에 만족하게 하셨느니라</h3><p>하고 <u>18</u>이렇게 말하여 겨우 무리를 말려 자기들에게 제사를 못하게 하니라<br />   <u>19</u>유대인들이 안디옥과 이고니온에서 와서 무리를 충동하니 그들이 돌로 바울을 쳐서 죽은 줄로 알고 시외로 끌어 내치니라 <u>20</u>제자들이 둘러섰을 때에 바울이 일어나 그 성에 들어갔다가 이튿날 바나바와 함께 더베로 가서 <u>21</u>복음을 그 성에서 전하여 많은 사람을 제자로 삼고 루스드라와 이고니온과 안디옥으로 돌아가서 <u>22</u>제자들의 마음을 굳게 하여 이 믿음에 머물러 있으라 권하고 또 우리가 하나님의 나라에 들어가려면 많은 환난을 겪어야 할 것이라 하고 <u>23</u>각 교회에서 장로들을 택하여 금식 기도 하며 그들이 믿는 주께 그들을 위탁하고 <u>24</u>비시디아 가운데로 지나서 밤빌리아에 이르러 <u>25</u>말씀을 버가에서 전하고 앗달리아로 내려가서 <u>26</u>거기서 배 타고 안디옥에 이르니 이 곳은 두 사도가 이룬 그 일을 위하여 전에 하나님의 은혜에 부탁하던 곳이라<br />   <u>27</u>그들이 이르러 교회를 모아 하나님이 함께 행하신 모든 일과 이방인들에게 믿음의 문을 여신 것을 보고하고 <u>28</u>제자들과 함께 오래 있으니라</p>","path":"/357","Question1":"이 부분의 성경말씀이 당신이 길러야 할 성품 자질에 대해서 말하고 있습니까? 있다면 무엇입니까? ","Question2":"당신은 삶에서 어떤 행동을 시작 할 수 있습니까?","Question3":"이 부분의 성경말씀이 뿌리 뽑아야 하는 당신의 성격의 결함을 다루고 있습니까? 무엇입니까?","Question4":"당신은 삶에서 어떤 행동을 시작 할 수 있습니까?","prevPath":"/356","nextPath":"/358"}},"pathContext":{}}

